* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: "Karla", sans-serif;
}

body {
   background-color: #1a1a1a;
   color: #ffffff;
   min-height: 100vh;
}

/* Header Styles */
.header {
   background: linear-gradient(90deg, #672280 1.18%, #a626d3 100%);
   color: #ffffff;
   display: flex;
   align-items: center;
   justify-content: space-around;
   height: 65px;
   padding: 20px;
}

.logo {
   display: flex;
   align-items: center;
   justify-content: center;
}

.logo img {
   width: 40px;
   height: 30px;
   margin-right: 10px;
}

.logo h2 {
   margin-right: auto;
   font-size: 20px;
}

/* Main Content */
main {
   padding: 2rem;
   max-width: 800px;
   margin: 0 auto;
}

/* Form Styles */
.form {
   display: grid;
   grid-template-columns: 1fr 1fr;
   gap: 20px;
   margin-bottom: 2rem;
}

.form input {
   padding: 0.8rem;
   border: 2px solid #d1d5db;
   border-radius: 6px;
   font-size: 1rem;
   transition: all 0.3s ease;
}

.form input:focus {
   outline: none;
   border-color: #a626d3;
   box-shadow: 0 0 0 3px rgba(166, 38, 211, 0.2);
}

.form button {
   grid-column: 1 / -1;
   padding: 0.8rem;
   background: linear-gradient(90deg, #672280 0%, #a626d3 100%);
   color: white;
   border: none;
   border-radius: 6px;
   font-size: 1.1rem;
   font-weight: 700;
   cursor: pointer;
   transition: transform 0.2s ease;
}

.form button:hover {
   transform: translateY(-2px);
}

/* Meme Display */
.meme-container {
   display: flex;
   justify-content: center;
   margin-top: 2rem;
}

.meme {
   position: relative;
   display: flex;
   justify-content: center;
   max-width: 100%;
}

.meme-image {
   max-width: 100%;
   max-height: 500px;
   border-radius: 8px;
   box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.meme-text {
   position: absolute;
   width: 100%;
   text-align: center;
   left: 50%;
   transform: translateX(-50%);
   padding: 0 1rem;
   font-family: "Impact", sans-serif;
   font-size: 2.5rem;
   text-transform: uppercase;
   color: white;
   letter-spacing: 1px;
   text-shadow:
      2px 2px 0 #000,
      -2px -2px 0 #000,
      2px -2px 0 #000,
      -2px 2px 0 #000,
      0 2px 0 #000,
      2px 0 0 #000,
      0 -2px 0 #000,
      -2px 0 0 #000,
      2px 2px 5px #000;
}

.top {
   top: 20px;
}

.bottom {
   bottom: 20px;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
   .header {
      padding: 0 1rem;
   }

   .form {
      grid-template-columns: 1fr;
      gap: 15px;
   }

   .meme-text {
      font-size: 2rem;
   }

   .meme-image {
      max-height: 400px;
   }
}

@media (max-width: 480px) {
   main {
      padding: 1.5rem;
   }

   .logo h2 {
      font-size: 1.2rem;
   }

   .form input,
   .form button {
      padding: 0.7rem;
      font-size: 1rem;
   }

   .meme-text {
      font-size: 1.5rem;
   }

   .meme-image {
      max-height: 300px;
   }

   .top {
      top: 10px;
   }

   .bottom {
      bottom: 10px;
   }
}